.pokemon-details {
  --background: none;
  margin: auto;
  color: #000;
  font-size: 14px;
  /*text-transform: capitalize;*/
  &::part(content) {
    max-width: 350px;
    max-height: 540px;
    border-radius: 24px;
  }
}

p.italicize {
  font-style: italic;
  font-weight: bold;
  font-size: 11px;
  border: 3px solid gold;
  border-radius: 6px;
  margin: 0;
  padding: 5px;
  text-align: center;
}

span.label {
  font-weight: bold;
  font-size: 16px;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

div.nameimageinfo {
  width: 310px;
  height: 500px;
  padding: 10px;
  border: 10px solid gold;
  border-radius: 20px;
}

div.headshot {
  padding: 0;
  margin: 0;
  border: 5px yellow outset;
  box-shadow: 3px 3px 10px #000;
  height: 200px;
  width: 300px;
  background-image: url(https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/80ff523f-ff84-457d-a547-464588d3a3d3/dfkhdfz-cb78baf6-3ba2-4cc6-b540-3d89f7f15beb.png/v1/fill/w_1083,h_738,q_70,strp/route_background_3_by_willdinomaster55_dfkhdfz-pre.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..jKlllK7aEt0ns003ngWKG4kZfXMjcb5WPbni9sU9Utg);
  background-size: cover;
}
img.headshot {
  max-height: 100%;
  max-width: 100%;
  display: block;
  margin: auto;
}

img.onTop {
  position: absolute;
  margin: 5px;
  padding: 5px;
  width: 25px;
  height: 25px;
  border-radius: 20px;
  border: 1px rgba(255, 255, 255, 0.5) solid;
  background-color: rgba(255, 255, 255, 0.5);
}

