/* Font styling */

div {
  font-family: sans-serif;
}

td.basic {
  font-weight: bold;
  font-size: 11px;
  margin: 0;
  padding: 0;
  width: 10%;
}

img.basic {
  width: 25px;
  height: 25px;
}

span.basic {
  border-radius: 10%;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.2);
  background-color: rgba(255, 215, 0, 0.2);
}

td.nameofanimal {
  font-weight: bold;
  font-size: 18px;
  margin: 0;
  padding: 0;
}

td.hp {
  color: crimson;
  font-size: 20px;
  text-align: right;
  font-weight: bold;
  width: 30%;
}

td.typesign {
  font-size: 20px;
  text-align: center;
  width: 20%;
}
img.typesign {
  box-shadow: 1px 1px 3px black;
  border-radius: 50px;
  width: 20px;
  height: 20px;
}

p.description {
  padding: 2px 0 0 0;
  margin: 0 0 5px 0;
  background: linear-gradient(to right, goldenrod, yellow, goldenrod);
  font-size: 11px;
  font-family: sans-serif;
  font-style: italic;
  font-weight: bold;
  text-align: center;
}

span.labeltext {
  font-size: 11px;
}

td.energy {
  width: 18%;
  font-size: 20px;
  text-align: center;
}

td.damage {
  font-weight: bold;
  text-align: right;
}

table.costs {
  font-size: 11px;
  text-align: center;
  width: 100%;
}

tr.costheaders {
  font-weight: bold;
}

tr.costicons {
  font-size: 20px;
}

span.costicons {
  padding-right: 3px;
}

img.costicons {
  width: 15px;
  height: 15px;
  box-shadow: 1px 1px 3px black;
  border-radius: 50px;
}

table.nameheaders {
  width: 100%;
}

table.stats {
  width: 100%;
  text-align: center;
}

p.bottomtext {
  font-size: 7px;
  text-align: center;
}
